  border: none;
  cursor: pointer;
  -webkit-transition: background 0.3s;
  transition: background 0.3s;
  z-index: 4;
  margin-bottom: 10vh; }

.cmn-toggle-switch:focus {
  outline: none; }

.cmn-toggle-switch-white span {
  background: black !important; }
  .cmn-toggle-switch-white span::before {
    background-color: black !important; }
  .cmn-toggle-switch-white span::after {
    background-color: black !important; }

.active > span::before {
  background-color: white !important; }

.active > span::after {
  background-color: white !important; }

.cmn-toggle-switch span {
  display: block;
  position: absolute;
  top: 45px;
  left: 50px;
  right: 18px;
  height: 3px;
  background: white;
  border-radius: 3px;
  z-index: 4; }

.cmn-toggle-switch span::before,
.cmn-toggle-switch span::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #fff;
